@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Pacifico&family=Poppins&display=swap);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{ 
  background-color: black;
  margin: 0;
}

 h1, h2 {
  font-family: 'Bebas Neue', cursive;
}

h3, h4 {
  font-family: 'Pacifico', cursive;

}

p, span, a {
  font-family: 'Poppins', sans-serif;
}

p {
  padding-left: 2em;
  padding-right: 2em;
}

li a {
  display: inline;
}

a {
  color: black;
}

a:visited {
  color: grey;
}

.App {
  text-align: center;
}


.textDiv {
  background-color: white;
  padding-top: 40px;
}

.section {
  display: flex;
  flex-wrap: wrap;
  margin-top: 5em;
  margin-bottom: 5em;
}

.section img {
  height: 400px;
  width: 100%;
  object-fit: cover;
}


@media only screen and (min-width: 600px) {

  main {
    top: 47px;
  }

  .textDiv {
    padding-top: 0;
  }

  .section div{
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
  }
}



.navbar {
  position: fixed;
  width: 100%;
  top: 0;
  z-Index: 1;
  background-color: white;
  /* padding-bottom: 30px; */
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 90%, 0 100%);
          clip-path: polygon(0 0, 100% 0, 100% 90%, 0 100%);
  transition: top 250ms ease-in-out;
}

.navBorder {
  height: 25px;
  background-color:black;
  -webkit-clip-path: polygon(0 80%, 100% 0, 100% 90%, 0 100%);
          clip-path: polygon(0 80%, 100% 0, 100% 90%, 0 100%);
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    padding-bottom: 30px;
  }

  .navbar li a {
    display: block;
    width: 100%;
    text-align: center;
    margin: auto;
    text-decoration: none;
  }

  .navbar li a {
    background-color: white;
  }

  li a:hover {
    background-color: rgb(199, 194, 194);
  }

  li a.active {
    background-color: grey;
  }

  .hamburgerIcon {
    position: fixed;
    right: 5px;
    top: 5px;
    height: auto;
    width: 40px;
    border: 1px solid black;
    padding: 2px;
    background-color: white;
    z-Index: 1;
  }



  @media only screen and (min-width: 600px) {
    ul {
      padding: 0;
    }
    .navBorder {
      display: none;
    }

    .hamburgerIcon {
      display: none;
    }

    .navbar {
      display: block;
      width: 100%;
      padding: 10px;
      background-color: white;
      border-bottom: black 2px solid;
      padding-bottom: 10px;
      -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
              clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }

    .navbar li {
      display: inline;
      margin-top: 10px
    }

    .navbar li a{
        display: inline;
        padding-left: 10px;
        padding-right: 10px;
    }

    .section div{
      width: 50%;
      display: flex;
      flex-wrap: wrap;
      align-content: center;
      justify-content: center;
    }
  }
banner {
    position: relative;
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items: middle;
}

.bannerLogo {
    position: absolute;
    background-color: rgba(255, 255, 255, 0.534);
    width: 100%;
    height: 400px;
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items: middle;
}

.bannerLogo h1 {
    font-size: 6em;
    margin: 0;
}

.bannerLogo h2 {
    font-size: 3em;
}

.bannerLogo {
}

banner img {
    width: 100%;
    height: 600px;
    object-fit: cover;
}
#about {
    margin-top: 50px;
    -webkit-clip-path: polygon(0 8%, 100% 0, 100% 98%, 0 90%);
            clip-path: polygon(0 8%, 100% 0, 100% 98%, 0 90%);
    background-color: white;
}

#about img {
    height: 230px;
    width: 100%;
    object-fit: cover;
    margin: none;
  }

.aboutImg1 {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

.aboutImg2 {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 90%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 90%);
}




  @media only screen and (min-width: 725px) {
    #about {
        margin-top: 100px;
        display: flex;
        flex-wrap: wrap;
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
                clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }

    #about div{
      width: 33.3%;
      display: flex;
      flex-wrap: wrap;
      align-content: center;
      justify-content: center;
    }

    .aboutImg1 {
        -webkit-clip-path: polygon(0 0, 100% 0, 95% 100%, 0 100%);
                clip-path: polygon(0 0, 100% 0, 95% 100%, 0 100%);
    }

    .aboutImg2 {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 5% 100%);
                clip-path: polygon(0 0, 100% 0, 100% 100%, 5% 100%);
    }


  }
#contact {
    width: 100%;
    background-color: white;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    min-height: 400px;
}

#contact {
    padding-bottom: 30px;
    padding-top: 10px;
}

#contact input,
#contact textarea {
    margin: 5px;
    text-align: right;

}
#visit{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    background-color: black;
    color: white;
    flex-direction: row-reverse;
    width: 100%;
    padding-bottom: 100px;
}


.locationPhoto {
    background-color: black;
}

.locationPhoto img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    -webkit-clip-path: polygon(100% 69%, 16% 0, 73% 0, 100% 50%, 100% 100%, 0 75%, 0 0);
            clip-path: polygon(100% 69%, 16% 0, 73% 0, 100% 50%, 100% 100%, 0 75%, 0 0);
}

.footer {
    background-color: white;
    padding-top: 80px;
    padding-bottom: 50px;
    text-align: right;
    padding-left: 20px;
    padding-right: 20px;
    -webkit-clip-path: polygon(0 0, 100% 15%, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 15%, 100% 100%, 0 100%);
}

.footer p {
    margin: 0;
    padding-right: 0;
}
.News {
    color: white;
    overflow: hidden;
    margin-top: 50px;
}

.NewsBox {
    display: flex;
    flex-wrap: none;
    left: -500px;
    position: relative;
    transition: left 500ms ease-in-out;
}

.NewsItem {
    width: 100vw;
    min-height: 300px;
    display: flex;
    flex-wrap: wrap;
}

.NewsItem img {
    object-fit: cover;
    width: 100%;
    height: 200px;
}

.NewsScroller {
    height: 10px;
    display: flex;
    justify-content: space-between;
}

.scrollerButton {
    height: 100%;
    margin-left: 10px;
    margin-right: 10px;
    transition: background-color 250ms ease-in-out;
}

@media only screen and (min-width: 600px) {

    .NewsItem div{
      width: 50%;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      align-content: center;
      justify-content: center;
    }

    .NewsItem img {
        width: 50%;
        height: 100%;
    }
  }


